// Fichero src/services/local-storage.js

// Funci贸n que obtiene una propiedad del local storage
// Si esta propiedad no existe porque es la primera vez que la usuaria entra en la p谩gina la funci贸n devuelve el valor de defaultValue
// Que esta funci贸n devuelva un valor por defecto es una c贸moda manera de trabajar, as铆 esta comprobaci贸n no la tenemos que hacer App.js
const get = (key, defaultValue) => {
    const localStorageData = localStorage.getItem(key);
    if (localStorageData === null) {
        return defaultValue;
    } else {
        return JSON.parse(localStorageData);
    }
};

// Funci贸n que guarda una propiedad y su valor en el local storage
const set = (key, value) => {
    const localStorageData = JSON.stringify(value);
    localStorage.setItem(key, localStorageData);
};

// Funci贸n que borra una propiedad del local storage
const remove = (key) => {
    localStorage.removeItem(key);
};
// Funci贸n que limpia todo el local storage
const clear = () => {
    localStorage.clear();
};
// Creamos un objeto temporal que es el que queremos exportar
// Este objeto tiene la una propiedad get cuyo valor es la funci贸n get
// Este objeto tiene la una propiedad set cuyo valor es la funci贸n set
// Este objeto tiene la una propiedad remove cuyo valor es la funci贸n remove
// Este objeto tiene la una propiedad clear cuyo valor es la funci贸n clear
const objectToExport = {
    get: get,
    set: set,
    remove: remove,
    clear: clear,
};

// Exportamos el objeto para que pueda ser usado desde App
export default objectToExport;
